import { FunctionalComponent, h } from '@stencil/core';

export type FcInputHelperTextType = {
  text: string;
  id: string;
};

export const FcInputHelperText: FunctionalComponent<FcInputHelperTextType> = props => {
  return (
    <div class="ath-input__helper-text" id={props.id}>
      <span>{props.text}</span>
    </div>
  );
};
